html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Space Grotesk", sans-serif;
  min-height: 100%;
  font-weight: 500;
}

.contenedor {
  width: 95%;
  max-width: 1000px;
  margin: 0 auto;
}

body {
  background-image: linear-gradient(to bottom, transparent 33%, hsl(0, 0%, 100%) 33%), url(../img/bg-main-mobile.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  body {
    background-image: linear-gradient(to right, transparent 33%, hsl(0, 0%, 100%) 33%), url(../img/bg-main-desktop.png);
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media (min-width: 768px) {
  main {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 5rem;
}
@media (min-width: 768px) {
  .form {
    padding-bottom: 0;
  }
}
.form__contenedor {
  width: 90%;
}
@media (min-width: 768px) {
  .form__contenedor {
    width: 75%;
  }
}
.form__active {
  display: none;
}

.field {
  margin-bottom: 2rem;
}
.field__label {
  display: block;
  color: hsl(278, 68%, 11%);
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 700;
}
.field__input {
  width: 100%;
  margin-top: 0.5rem;
  letter-spacing: 0.1rem;
  font-size: 1.4rem;
  font-weight: 400;
  padding: 1rem;
  border: 2px solid #f1f1f1;
  border-radius: 1rem;
}
.field__input--inline {
  width: 45%;
  display: inline-block;
}
.field__input--inline:first-of-type {
  margin-right: 5%;
}
.field__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
.field__input-flex {
  display: flex;
  column-gap: 1rem;
}
.field__submit {
  display: block;
  width: 100%;
  padding: 1.5rem 0;
  color: hsl(270, 3%, 87%);
  background-color: #c283e7;
  border-radius: 0.5rem;
  border: none;
  text-align: center;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 500;
  margin-top: 1.5rem;
}

.submitViolette {
  background-color: hsl(278, 68%, 11%);
}

.borderRed {
  border: 1px solid red;
}

.note {
  margin: 0.5rem 0 0 0;
  color: red;
  font-size: 1.1rem;
}

.thanks {
  width: 75%;
  display: none;
}
.thanks__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.thanks__advice {
  font-size: 3.2rem;
  color: hsl(278, 68%, 11%);
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .thanks__advice {
    font-size: 2.8rem;
  }
}
.thanks__description {
  font-size: 1.8rem;
  margin: 0 0 4rem 0;
  color: hsl(279, 6%, 55%);
  text-align: center;
}
@media (min-width: 768px) {
  .thanks__description {
    font-size: 1.5rem;
  }
}
.thanks__boton {
  display: block;
  width: 100%;
  padding: 1.5rem 0;
  color: hsl(270, 3%, 87%);
  background-color: #c283e7;
  border-radius: 0.5rem;
  border: none;
  text-align: center;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 500;
  background-color: hsl(278, 68%, 11%);
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .thanks__boton {
    font-size: 1.5rem;
  }
}
.thanks__active {
  display: block;
}

.front {
  margin-top: -7.5rem;
  z-index: 1;
}
.front__contenedor {
  width: 30rem;
  height: 17.5rem;
  background-image: url(../img/bg-card-front.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding: 2.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  letter-spacing: 0.1rem;
  color: hsl(0, 0%, 100%);
}
@media (min-width: 768px) {
  .front__contenedor {
    width: 35rem;
    height: 20rem;
  }
}
.front__img {
  width: 4rem;
}
@media (min-width: 768px) {
  .front__img {
    width: 7rem;
  }
}
.front__card-number {
  font-size: 1.4rem;
  margin-bottom: 0;
  letter-spacing: 0.2rem;
}
@media (min-width: 768px) {
  .front__card-number {
    font-size: 2.2rem;
  }
}
.front__data {
  display: flex;
  justify-content: space-between;
  margin-top: 3rem;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 400;
}
@media (min-width: 768px) {
  .front__data {
    font-size: 1.2rem;
  }
}
.front__name, .front__date {
  margin: 0;
}

.behind {
  display: flex;
  justify-content: flex-end;
}
@media (min-width: 768px) {
  .behind {
    margin-top: 3rem;
  }
}
.behind__contenedor {
  width: 30rem;
  height: 17.5rem;
  background-image: url(../img/bg-card-back.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding: 4rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
@media (min-width: 768px) {
  .behind__contenedor {
    width: 35rem;
    height: 20rem;
  }
}
.behind__cvc {
  color: hsl(0, 0%, 100%);
  font-size: 1rem;
}
@media (min-width: 768px) {
  .behind__cvc {
    font-size: 1.2rem;
  }
}

.cards {
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .cards {
    display: block;
    flex-direction: none;
    margin-bottom: 0;
  }
}